import React, { useState } from 'react';
import { Button, Popover, PopoverHeader, PopoverBody } from 'reactstrap';
import { costumers } from '../shared/costumers';

const Example = (props) => {
   
    const warehouses1 = props.costumer1.warehouses.map((w) => {
        return(
            <div>
                <strong>{w}</strong>
            </div>
        )
    })
    
  const [popoverOpen, setPopoverOpen] = useState(false);

  const toggle = () => setPopoverOpen(!popoverOpen);
    //alert(JSON.stringify(props.costumer1.name))
  return (
    <div>
      <div id={props.costumer1.name} type="button" onClick={toggle}>
        <b>View</b>
      </div>
      <Popover placement="bottom" isOpen={popoverOpen} target={props.costumer1.name} toggle={toggle}>
        <PopoverHeader>Warehouses</PopoverHeader>
        <PopoverBody>{warehouses1}</PopoverBody>
      </Popover>
    </div>
  );
}

export default Example;